"use client";

import React from 'react';
import {
    Blinds,
    Scroll,
    GalleryVerticalEnd,
    FoldHorizontal,
    GalleryHorizontalEnd,
    Image,
    AlignJustify,
    Wrench
} from 'lucide-react';
import Link from 'next/link';

const categories = [
    { id: 'wooden-blinds', name: 'มู่ลี่ไม้', icon: <AlignJustify size={20} /> },
    { id: 'roller-blinds', name: 'ม่านม้วน', icon: <Scroll size={20} /> },
    { id: 'curtains', name: 'ผ้าม่าน', icon: <GalleryVerticalEnd size={20} /> }, // Closest to draped curtains
    { id: 'pvc-partition', name: 'ฉากกั้น PVC', icon: <FoldHorizontal size={20} /> },
    { id: 'vertical-blinds', name: 'ม่านปรับแสง', icon: <GalleryHorizontalEnd size={20} /> },
    { id: 'wallpaper', name: 'วอลล์เปเปอร์', icon: <Image size={20} /> },
    { id: 'alum-blinds', name: 'มู่ลี่อลูมิเนียม', icon: <Blinds size={20} /> },
    { id: 'accessories', name: 'อุปกรณ์/อื่นๆ', icon: <Wrench size={20} /> },
];

export default function CategoryList() {
    const [active, setActive] = React.useState('all');

    return (
        <div style={{ marginBottom: '2rem' }}>
            <div style={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
                marginBottom: '1rem',
                padding: '0 0.5rem'
            }}>
                <h2 style={{ fontSize: '1.25rem', fontWeight: 700 }}>หมวดหมู่</h2>
                <Link href="/products" style={{ color: 'var(--color-secondary)', fontSize: '0.9rem' }}>ดูทั้งหมด</Link>
            </div>

            <div style={{
                display: 'flex',
                gap: '1rem',
                overflowX: 'auto',
                paddingBottom: '1rem',
                scrollbarWidth: 'none',
                msOverflowStyle: 'none'
            }} className="no-scrollbar">
                {categories.map((cat) => (
                    <button
                        key={cat.id}
                        onClick={() => setActive(cat.id)}
                        style={{
                            display: 'flex',
                            flexDirection: 'column',
                            alignItems: 'center',
                            gap: '0.5rem',
                            minWidth: '80px',
                            background: 'none',
                            border: 'none',
                            cursor: 'pointer'
                        }}
                    >
                        <div style={{
                            width: '60px',
                            height: '60px',
                            borderRadius: '20px',
                            backgroundColor: active === cat.id ? 'var(--color-primary)' : 'var(--surface)',
                            color: active === cat.id ? 'white' : 'var(--color-primary)',
                            display: 'flex',
                            justifyContent: 'center',
                            alignItems: 'center',
                            boxShadow: active === cat.id ? 'var(--shadow-lg)' : 'var(--shadow-soft)',
                            transition: 'all 0.3s ease'
                        }}>
                            {cat.icon}
                        </div>
                        <span style={{
                            fontSize: '0.85rem',
                            color: active === cat.id ? 'var(--color-primary)' : 'var(--color-secondary)',
                            fontWeight: active === cat.id ? 600 : 400
                        }}>
                            {cat.name}
                        </span>
                    </button>
                ))}
            </div>
            <style jsx global>{`
                .no-scrollbar::-webkit-scrollbar {
                    display: none;
                }
            `}</style>
        </div>
    );
}
